        <div class="container">
            <h4><i class="material-icons-outlined infoIcon">contact_page</i> <span class="contTop">CREA NEWS</span></h4>
            <b-form @submit="onSubmit" @reset="onReset">

                <div class="titleSectionCalcolatore">
                    <div class="titleCalcolatore">Titolo<span class="asterisco">*</span></div>
                </div>
                <b-form-input id="input-2" v-model="form.title" required placeholder="Inserisci il titolo"></b-form-input>

                <div class="titleSectionCalcolatore">
                    <div class="titleCalcolatore">Data<span class="asterisco">*</span></div>
                </div>
                <b-form-datepicker id="example-datepicker" v-model="form.data" class="mb-2" required></b-form-datepicker>

                <div class="titleSectionCalcolatore">
                    <div class="titleCalcolatore">Url Immagine<span class="asterisco">*</span></div>
                </div>
                <b-form-input id="input-2" v-model="form.urlImg" required placeholder="Inserisci link dell'immagine"></b-form-input>

                <div class="titleSectionCalcolatore">
                    <div class="titleCalcolatore">Testo<span class="asterisco">*</span></div>
                </div>

                <ckeditor v-model="form.html" :config="editorConfig"></ckeditor>
                <div class="submitDiv">
                    <b-button type="submit" class="btnSubmit">CREA</b-button>    
                </div>
                
            </b-form>
        </div>
    </div>
    
</template>     
<script>
import { mapActions } from 'vuex';
export default {
    components: {

    },
    data(){
        return{
            editorConfig: {
                    toolbar: [ [ 'Bold','Italic','Strike','-','Link','Image','-','NumberedList','BulletedList','-','Format','-','Source', ] ]
            },
            form:{
                title:'',
                data: new Date,
                urlImg:'',
                html: '',
            }
        }
    },methods:{
        ...mapActions(["createNews"]),
        onSubmit(evt){
            evt.preventDefault();
            this.createNews(this.form);
        },
        onReset(evt){
            evt.preventDefault();
            this.form.title = '';
            this.form.data = new Date();
            this.form.urlImg = '';
            this.form.html = '';
        }
    }
}
</script>

