<template>
    <div id="login">
        <div class="loginform">
            <img src="../../assets/imgs/logoBlack.png" class="imgForm" alt="">
            <h4>LOGIN</h4>
            <b-form  @submit="onSubmit" @reset="onReset" v-if="show">
                <div class="titleSectionCalcolatore">
                    <div class="titleCalcolatore">Email<span class="asterisco">*</span></div>
                </div>
                <b-form-input id="input-1" v-model="form.email" type="email" required placeholder="Inserisci l'email"></b-form-input>
                <div class="titleSectionCalcolatore">
                    <div class="titleCalcolatore">Password<span class="asterisco">*</span></div>
                </div>
                <b-input type="password" v-model="form.password" id="text-password" required aria-describedby="password-help-block" placeholder="Inserisi la password"></b-input>
                <b-button type="submit" class="btnSubmit" >LOGIN</b-button>
            </b-form>
        </div>
    </div>
</template>

<script>
const axios = require('axios');
import { Server } from '../../utils/variables'
export default {
    data(){
        return{
            show: true,
            form: {
                email: '',
                password: ''
            }
        }
    },methods:{
        onSubmit(evt){
            evt.preventDefault();
            axios
                .post(Server+'/user/login',{
                    email: this.form.email,
                    password: this.form.password 
                })
                .then(response => {
                    var data = response.data;
                    this.$parent.setAgente(data.user);
                    var token = data.token;
                    var nome = data.user.nome;
                    var cognome = data.user.cognome;
                    var id = data.user._id;
                    var email = data.user.email;
                    this.$cookies.set('token',token);
                    this.$cookies.set('nome',nome);
                    this.$cookies.set('cognome',cognome);
                    this.$cookies.set('id',id);
                    this.$cookies.set('email',email);
                    this.$parent.logged = true;
                    this.$router.push('paginaPersonale');
                })
                .catch(error => {
                    console.log(error);
                })
        },
        onReset(evt){
            evt.preventDefault();
            this.form.email = '';
            this.form.password = '';

        },
    },
    mounted(){
        if(this.$cookies.get('token')!=null){
            this.$router.push('paginaPersonale');
        }
        //this.$parent.makeToast('prova','description',500000,'danger','append');
    }
}
</script>

<style scoped>
    .loginform{
        box-shadow: 0 2px 2px 0px rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);;
        max-width: 600px;
        background-color: white;
        text-align: center;
        padding: 20px 20px 60px 20px;
        margin: 120px auto 50px auto;
    }
    h4{
        font-size: 20pt;
        font-weight: 600;
    }
    h4::before, h4::after{
        content: ' - ';
    }
.titleSectionCalcolatore{
    text-align: left;
    margin-left: 5px;
    display: block;
    margin-top: 10px;
}
.titleCalcolatore{
    font-weight: bold;
    font-style: normal;
}
.asterisco{
    color: var(--complementaryC);
}
.subTitleCalcolatore{
    font-weight: 400;
    font-size: 0.8em;
    color: gray;
    margin-top: 0px;
    font-style: italic;
}

.btnSubmit{
    width: 100%;
    max-width: 200px;
    margin-top: 20px;
    border: 1px solid var(--complementaryC);
    background-color: white;
    color: var(--complementaryC);
    font-weight: 600;
}

.btnSubmit:hover{
    background-color: var(--complementaryC);
    color: white;
}

.imgForm{
    width: 70vw;
    max-width: 300px;
}


</style>