<template>
    <div id="areaRiservata">
        <Navbar v-if="logged"></Navbar>
        <router-view/>
    </div>
</template>

<script>
import Navbar from '../components/area_riservata/navbarAreaRiservata'
export default {
    components: {
        Navbar,
    },
    data(){
        return{
            toastCount: 0,
            logged: false,
            agente: undefined,
        }
    },
    methods:{
        makeToast(title,delay,variant,append) {
            this.toastCount++;

            this.$bvToast.toast(' ', {
                title: title,
                variant: variant,
                autoHideDelay: delay,
                appendToast: append,
                solid: true
            })
        },
        setAgente(agente2){
            console.log('Setting Agente:');
            this.agente = agente2;
            console.log(this.agente);
        },
        getAgente(){
            return this.agente;
        }
        
    },mounted(){
        if(this.$cookies.get('token')!=null){
            this.logged = true;
        }
        if(this.$cookies.get('token')==null && this.$route.name != 'Area Riservata - Login'){
            this.$router.push('/area-riservata/login');
        }
        
    }
}
</script>

<style scoped>
    .toast-body{
        padding: 0px;
    }
</style>