import axios from 'axios';
import { Server } from '../../utils/variables';
import cookie from 'vue-cookies'

const state = {
    agenti:[],
    retrived: false,
    agentiFiltrati:[],
    agente:{},
    agentiExecutive:[],
    agentiTeamLeader:[],
    agentiTopExecutive:[],

};
const getters = {
    allAgenti: state => state.agenti,
    filtredAgenti: state => state.agentiFiltrati,
    retrived: state => state.retrived,
    agentiIsEmpty: state => {
        return state.agenti.length == 0
    },
    getAgente: (state) =>{
        return state.agente;
    },
    agentiExecutive: (state) => state.agentiExecutive,
    agentiTeamLeader: (state) => state.agentiTeamLeader,
    agentiTopExecutive: (state) => state.agentiTopExecutive,
};
const actions = {
    //async requests
    async fetchAgenti({ commit }){
        const config = {headers: { Authorization: `Bearer ${cookie.get('adminToken')}` }};
        const response = await axios.get(Server+'/user/getAll',config);
        console.log('Data Fatched');
        commit('setAgenti',response.data.users);
    },
    async updateAgenteAPI({commit},user){
        const config = {headers: { Authorization: `Bearer ${cookie.get('adminToken')}` }};
        const response = await axios.post(Server+'/user/editByAdmin',{
            id: user._id,
            update: user,
        },config);
        commit('updateAgente',response.data.user);
    },
    async createAgente({commit},user){
        const config = {headers: { Authorization: `Bearer ${cookie.get('adminToken')}` }};
        const response = await axios.post(Server+'/user/add',{
            user: user,
        },config);
        commit('addAgente',response.data.user);
    }
};

const mutations = {
    //sync requests
    setAgenti(state,agenti){
        state.agenti = agenti;
        state.agentiFiltrati = agenti;
        state.agentiExecutive = agenti.filter(agente => agente.figura == 'Executive');
        state.agentiTeamLeader = agenti.filter(agente => agente.figura == 'Team Leader');
        state.agentiTopExecutive = agenti.filter(agente => agente.figura == 'Top Executive');
        state.retrived = true;
    },
    addAgente(state,user){
        state.agenti.push(user);
        console.log('User Created');
    },
    setAgente(state,id){
        if(state.retrived){
            state.agente = state.agenti.find(element => element._id == id);
            if(state.agente.executive){state.agente.executive = state.agente.executive._id}else{state.agente.executive=''}
            if(state.agente.topExecutive){state.agente.topExecutive = state.agente.topExecutive._id}else{state.agente.topExecutive=''}
            if(state.agente.teamLeader){state.agente.teamLeader = state.agente.teamLeader._id}else{state.agente.teamLeader=''}
        }else{
            this.dispatch('fetchAgenti').then(()=>{
                state.agente = state.agenti.find(element => element._id == id);
                if(state.agente.executive){state.agente.executive = state.agente.executive._id}else{state.agente.executive=''}
                if(state.agente.topExecutive){state.agente.topExecutive = state.agente.topExecutive._id}else{state.agente.topExecutive=''}
                if(state.agente.teamLeader){state.agente.teamLeader = state.agente.teamLeader._id}else{state.agente.teamLeader=''}
            })
        }
    },
    updateAgente(state,user){
        console.log(user);
        const index = state.agenti.findIndex(agente => agente._id == user._id);
        if(index != -1){
            state.agenti[index] = user;
        }
    },
    resetListAgenti(state){
        console.log('Resetting List Agenti');
        state.agentiFiltrati = state.agenti;
    },
    setAgentiFiltrati(state,filters){
        console.log('Filtring: '+filters.name+'/'+filters.figura);
        var agentiFiltrati = state.agenti;
        if(filters.name!=''){
            console.log('Filtred by name');
            agentiFiltrati = agentiFiltrati.filter(filterName.bind(null,filters.name));    
        }
        if(filters.figura!='Tutti'){
            console.log('Filtred by figura');
            agentiFiltrati = agentiFiltrati.filter(filterFigura.bind(null,filters.figura)); 
        }
        state.agentiFiltrati = agentiFiltrati;
    }
};

function filterName(word,element){
    return element.nome.toLowerCase().includes(word.toLowerCase()) || element.cognome.toLowerCase().includes(word.toLowerCase());
}
function filterFigura(figura,element){
     return element.figura == figura;
}



export default{
    state,
    getters,
    actions,
    mutations
}