<template>
    <section id=timeline>
	<div class="demo-card-wrapper">
		<div :class="'demo-card demo-card--step'" v-for="(notizia,index) in news" :key="notizia._id"
            :style="(index+1)%2==0?'order:'+(index+2):'oder:'+(index+1)"
            @click="$router.push('/news/'+notizia._id)"
        >
			<div class="head">
				<div class="number-box">
					<span>{{index+1}}</span>
				</div>
				<h2><span class="small">{{formatDate(notizia.data)}}</span> {{formatTitle(notizia.title)}}</h2>
			</div>
			<div class="body">
				<img :src="notizia.urlImg" alt="Graphic">
			</div>
		</div>
	</div>
</section>
</template>

<script>
export default {
    props: ['news'],
    methods:{
        formatDate(date){
            var data = new Date(date);
            var giorno = data.getDate();
            if(giorno<10){giorno = '0'+giorno}
            var mese = data.getMonth();
            if(mese<10){mese = '0'+mese}
            var anno = data.getFullYear();
            return giorno+'/'+mese+'/'+anno;
        },
        formatTitle(title){
            var titolo = new String(title);
            var lunghezza = 45;
            var newTitle;
            if(titolo.length > lunghezza){
                newTitle = titolo.substring(0,lunghezza)+'...';
            }else{
                newTitle = titolo.substring(0,lunghezza);
            }
            return newTitle;
        }
    }
}
</script>

