<template>
    <div id="editAgente">
        <p class="subtitle fancy"><span>MODIFICA <b>AGENTE</b></span></p>
        <div class="container">
            <div v-if="loading!=0" class="spinning">
                <b-spinner style="width: 5rem; height: 5rem; font-size: 40pt;" label="Loading..." class="spinner2"></b-spinner>
            </div>
            <b-form @submit="onSubmit" @reset="onReset" v-if="agente!=null">
                <p class="info ">
                    <b-row class="inprow ">
                        <b-col class="inpcol " sm="5">
                            <b>Nome:</b>
                            <b-form-input v-model="agente.nome" size="sm" class="inputProfile" required></b-form-input>
                        </b-col>
                        <b-col class="inpcol " sm="5">
                            <b>Cognome:</b>
                            <b-form-input v-model="agente.cognome" size="sm" class="inputProfile" required></b-form-input>
                        </b-col>
                        <b-col class="inpcol " sm="2">
                            <b>Sesso:</b>
                            <b-form-select v-model="agente.sesso" :options="sessoOptions" size="sm" class="mt-3"></b-form-select>
                        </b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Email:</b></b-col>
                        <b-col class="inpcol" sm="8"><b-form-input v-model="agente.email" size="sm" class="inputProfile" required></b-form-input></b-col>
                    </b-row>
                </p>

                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Stato Account:</b></b-col>
                        <b-col class="inpcol" sm="8">
                            <select v-model="agente.status" class="mt-3 custom-select custom-select-sm">
                                <option :key="opt.value" v-for="opt in this.optionsStato" :value="opt.value">{{opt.name}}</option>
                            </select>
                        </b-col>
                    </b-row>
                </p>
                
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Ruolo/Figura:</b></b-col>
                        <b-col class="inpcol" sm="4"><b-form-select v-model="agente.ruolo" :options="optionsRuolo" size="sm" class="mt-3" required></b-form-select></b-col>
                        <b-col class="inpcol" sm="4"><b-form-select v-model="agente.figura" :options="optionsFigura" size="sm" class="mt-3" required></b-form-select></b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Data di Nascita:</b></b-col>
                        <b-col class="inpcol" sm="8"><b-form-datepicker id="example-datepicker" v-model="agente.dataDiNascita" size="sm" class="mt-3"></b-form-datepicker></b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Indirizzo:</b> </b-col>
                        <b-col class="inpcol" sm="6"><b-form-input v-model="agente.indirizzo" size="sm" class="inputProfile"></b-form-input></b-col>
                        <b-col class="inpcol" sm="2"><b-form-input v-model="agente.civico" size="sm" class="inputProfile"></b-form-input></b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Comune/Provincia:</b> </b-col>
                        <b-col class="inpcol" sm="6"><b-form-input v-model="agente.comune" size="sm" class="inputProfile"></b-form-input></b-col>
                        <b-col class="inpcol" sm="2"><b-form-input v-model="agente.provincia" size="sm" class="inputProfile"></b-form-input></b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>CAP/Regione:</b> </b-col>
                        <b-col class="inpcol" sm="4"><b-form-input v-model="agente.cap" size="sm" class="inputProfile"></b-form-input></b-col>
                        <b-col class="inpcol" sm="4"><b-form-input v-model="agente.regione" size="sm" class="inputProfile"></b-form-input></b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Codice Fiscale:</b></b-col>
                        <b-col class="inpcol" sm="8"><b-form-input v-model="agente.codiceFiscale" size="sm" class="inputProfile"></b-form-input></b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Cellulare:</b></b-col>
                        <b-col class="inpcol" sm="8"><b-form-input v-model="agente.cellulare" size="sm" class="inputProfile"></b-form-input></b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>OAM:</b></b-col>
                        <b-col class="inpcol" sm="8"><b-form-datepicker id="example-datepicker2" v-model="agente.oam" size="sm" class="mt-3"></b-form-datepicker></b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow">  
                        <b-col class="inpcol" sm="4"><b>Top Executive</b></b-col>
                        <b-col class="inpcol" sm="8">
                            <select v-model="agente.topExecutive" class="mt-3 custom-select custom-select-sm">
                                <option :key="agente._id" v-for="agente in agentiTopExecutive" :value="agente._id">{{agente.email}}</option>
                            </select>
                        </b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Executive</b></b-col>
                        <b-col class="inpcol" sm="8">
                            <select v-model="agente.executive" class="mt-3 custom-select custom-select-sm">
                                <option :key="agente._id" v-for="agente in agentiExecutive" :value="agente._id">{{agente.email}}</option>
                            </select>
                        </b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Team Leader</b></b-col>
                        <b-col class="inpcol" sm="8">
                            <select v-model="agente.teamLeader" class="mt-3 custom-select custom-select-sm">
                                <option :key="agente._id" v-for="agente in agentiTeamLeader" :value="agente._id">{{agente.email}}</option>
                            </select>
                        </b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow ">
                        <b-col class="inpcol padd" sm="3">
                            <b>%Mutui</b>
                            <b-form-input v-model="agente.percMutui" size="sm" class="inputProfile"></b-form-input>
                        </b-col>
                        <b-col class="inpcol padd" sm="3">
                            <b>%CDQ</b>
                            <b-form-input v-model="agente.percCDQ" size="sm" class="inputProfile"></b-form-input>
                        </b-col>
                        <b-col class="inpcol padd" sm="3">
                            <b>%Mediazioni</b>
                            <b-form-input v-model="agente.percMediazioni" size="sm" class="inputProfile"></b-form-input>
                        </b-col>
                        <b-col class="inpcol padd" sm="3">
                            <b>%P.P</b>
                            <b-form-input v-model="agente.perProvPrestiti" size="sm" class="inputProfile"></b-form-input>
                        </b-col>
                    </b-row>
                </p>
                <div class="submitDiv">
                    <button type="submit" class="btnProfile editBtn" >MODIFICA</button>    
                </div>
                
            </b-form>
        </div>
    </div>
</template>

<script>
import { mapMutations, mapGetters, mapActions } from 'vuex';
export default {
    name: 'ModificaAgente',
    data(){return{
        loading: false,
        sessoOptions: ['M','F'],
        optionsStato:[{value:'pending',name:'Da Confermare'},{value:'validated',name:'Confermato'},{value:'deleted',name:'Bloccato'}],
        optionsFigura:["Head Executive","Top Executive","Executive","Team Leader","Personal Broker","Dipendente"],
        optionsRuolo:["Agente","Dipendente"],

    }},
    computed: mapGetters({agente:'getAgente',agentiTopExecutive:'agentiTopExecutive',agentiTeamLeader:'agentiTeamLeader',agentiExecutive:'agentiExecutive',retrived:'retrived'}),
    mounted(){
        if(!this.retrived){
            this.fetchAgenti().then(()=>{
                this.setAgente(this.$route.params.id);
            });
        }else{
            this.setAgente(this.$route.params.id);
        }
    },
    methods:{
        onReset(evt){evt.preventDefault(); console.log(this.agente)},
        onSubmit(evt){evt.preventDefault(); console.log(this.agente); this.updateAgenteAPI(this.agente).then(()=>{
            this.$parent.makeToast('Utente Modificato',500,'success',true);
        })},
        ...mapMutations(['setAgente']),
        ...mapActions(['updateAgenteAPI','fetchAgenti']),
    }
}
</script>

<style scoped>
    #editAgente{
        margin-top: calc(var(--NavBarHeight) + 50px);   
        margin-bottom: 40px;
    }
    .container{
        box-shadow: 0 2px 2px 0px rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
        width: 100%;
        background-color: white;
        text-align: left;
        padding: 20px 20px 20px 20px;
        max-width: 600px;
    }
    .mt-3{
        margin-top: 0px!important;
    }
    .submitDiv{
        width: 100%;
        text-align: center;
    }
    .btnProfile{
        margin-top: 10px;
        width: 100%;
        max-width: 200px;
        background-color: white;
        border: 1px solid green;
        color: green;
        font-weight: 700;
    }
    .btnProfile:hover{
        background-color: green;
        color: white;
    }

    .editBtn{
        border-color: rgb(68, 68, 255);
        color: rgb(68, 68, 255);
        cursor: pointer;
    }
    .editBtn:hover{
        background-color: rgb(68, 68, 255);
        color: white;
    }
    .spinning{
        margin-top: 200px;
        margin-bottom: 200px;
        text-align: center;
    }
    .preIMG{
        text-align: center;
        width: 100%;
        margin-bottom: 10px;
    }
    .imgProfile{
        max-width: 50%;
    }
</style>