<template>
    <div id="listaAccessi">
        <p class="subtitle fancy"><span>LISTA <b>ACCESSI</b></span></p>
        <div class="container">

        <b-list-group>
            <b-list-group-item  class="flex-column align-items-start itemFirst">
                <div class="d-flex w-100 justify-content-between">
                    <h5 class="mb-1">
                        <span class="nome">Nome</span>
                        <span class="cognome">Cognome</span> 
                        <span class="figura">Figura - IP</span>
                    </h5>
                    <small class="date">Data - Ora</small>
                </div>
            </b-list-group-item>
            <div v-if="loading">
                <b-list-group-item class="flex-column align-items-start " v-for="index in 30" :key="index">
                    <b-skeleton width="100%" class="skeleton"></b-skeleton>
                </b-list-group-item>
            </div>

            <transition name="fade">
                <div v-if="!loading">
                    <b-list-group-item :href=accesso.user._id class="flex-column align-items-start" v-for="accesso in accessi" :key="accesso._id">
                        <div class="d-flex w-100 justify-content-between">
                            <h5 class="mb-1">
                                <span class="nome">{{formatName(accesso.user.nome)}}</span>
                                <span class="cognome">{{formatName(accesso.user.cognome)}}</span> 
                                <span class="figura">{{formatName(accesso.user.figura)}} - {{accesso.ip}}</span>
                            </h5>
                            <small class="date">{{formatDate(accesso.data)}}</small>
                        </div>
                    </b-list-group-item>
                </div>
            </transition>
        </b-list-group>
        </div>
    </div>
    
</template>

<script>
const axios = require('axios');
import { Server } from '../../utils/variables'
export default {
    data(){
        return{
            loading: false,
            accessi: [],
        }
    },mounted(){
        this.loading = true;
        const config = {
            headers: { Authorization: `Bearer ${this.$cookies.get('adminToken')}` }
        };
        axios.get(Server+'/admin/getAllAccessi',config)
            .then(response =>{
                this.accessi = response.data.accessi.reverse();
                this.loading = false;
            })
    },methods:{
        formatDate(date){
            var data = new Date(date);
            var day = data.getDate();
            var mese = data.getMonth()+1;
            var anno = data.getFullYear();
            var ora = data.getHours();
            var minut = data.getMinutes();
            if(minut<10){minut = '0'+minut}
            return day+'/'+mese+'/'+anno+' - '+ora+':'+minut;
        },
        formatName(name){
            return name.charAt(0).toUpperCase() + name.slice(1);
        }
    }
}
</script>

<style scoped>
    .container{
        max-width: 500px;
    }
    #listaAccessi{
        margin-top: calc(var(--NavBarHeight) + 50px);
        margin-bottom: 40px;
    }
    .list-group-item{
        padding: 4px 10px 4px 10px;
        border-bottom: 1px inherit white;
    }
    .nome{
        font-weight: 400;
        line-height: 12px;
    }
    .cognome{
        font-weight: 400;
        padding-left: 5px;
        line-height: 12px;
    }
    .figura{
        font-weight: 500;
        font-size: 10pt;
        padding-left: 10px;
    }
    .mb-1{
        margin-bottom: 0px!important;
    }
    .date{
        margin-top: 2px;
    }
    .itemFirst{
        border-bottom: 2px solid rgba(0,0,0,.125);
    }
    .skeleton{
        width: 100%;
        margin-bottom: 0px;
    }
    .noPadding{
        padding: 0;
    }

</style>