<template>
    <div id="adminFormazione">
        <p class="subtitle fancy"><span><b>VIDEO FORMAZIONE</b></span></p>
        <div class="container">
            <div class="filters">
                <b-row class="my-1">
                    <b-col sm="6">
                        <label for="input-small" class="overInput">Hashtags:</label>
                        <vue-tags-input
                        v-model="tagFilter"
                        :tags="tagsFilter"
                        :autocomplete-items="filtredTags"
                        @tags-changed="newTags => tagsFilter = newTags"
                        :add-only-from-autocomplete="true"
                        />
                    </b-col>
                    <b-col sm="6">
                        <label for="input-small" class="overInput">Figura:</label>
                        <b-form-select size="sm" v-model="filterArgomentoSelected" :options="filterArgomentoOptions"></b-form-select>                
                    </b-col>
                </b-row>
            </div>
            <b-row>
                <b-col sm="4" v-for="formazione in filtredFormazioni" :key="formazione._id" class="blocco">
                    <transition name="fade">
                    <div class="element">
                        <span class="argomento">{{formazione.argomento}}</span><span class="data">{{reformatDate(formazione.data)}}</span>
                        <div class="title">
                            {{formazione.titolo}}
                        </div>
                        <iframe :src="'https://player.vimeo.com/video/'+formazione.urlVideo" width="100%" frameborder="0" allow="autoplay; fullscreen" allowfullscreen></iframe>
                        <div class="description">
                            <p class="descrizioneTesto">{{formazione.descrizione}}</p>
                        </div>
                        <div class="description">
                            <span class="hashtag" v-for="(hashtag,index) in formazione.hashtag" :key="index">#{{hashtag}}</span>
                        </div>
                        <div style="clear:both;"></div>
                        <div class="actions">
                            <b-row>
                                <b-col><div class="button btnModifica" @click="opendModal(formazione._id)">MODIFICA</div></b-col>
                                <b-col><div class="button btnCancella" v-b-modal="'cancella'+formazione._id">CANCELLA</div></b-col>
                            </b-row>
                        </div>
                        <b-modal title="SEI SICURO?" :id="'cancella'+formazione._id" hide-footer>
                            <b-row>
                                <b-col><div class="button btnCancella" @click="cancella(formazione._id)">SI</div></b-col>
                                <b-col><div class="button btnVisualliza" @click="hideModal('cancella'+formazione._id)">NO</div></b-col>
                            </b-row>
                        </b-modal>
                    </div>
                    </transition>
                </b-col>
            </b-row>
        </div>
        <b-modal :title="'MODIFICA'" :id="'modifica'" hide-footer>
            <b-form >
                <div class="titleSectionCalcolatore">
                <div class="titleCalcolatore">Titolo<span class="asterisco">*</span></div>
                <b-form-input id="input-2" size="sm" v-model="titolo" required placeholder="Inserisci il Titolo"></b-form-input>
            </div>
            <div class="titleSectionCalcolatore">
                <div class="titleCalcolatore">Data<span class="asterisco">*</span></div>
                <b-form-datepicker id="example-datepicker" size="sm" v-model="data" class="mb-2" required></b-form-datepicker>
            </div>
            <div class="titleSectionCalcolatore">
                <div class="titleCalcolatore">Indirizzo Video<span class="asterisco">*</span></div>
                <b-form-input id="input-2" size="sm" v-model="urlVideo" required placeholder="Inserisci l'indirizzo video"></b-form-input>
            </div>
            <div class="titleSectionCalcolatore">
                <div class="titleCalcolatore">Argomento<span class="asterisco">*</span></div>
                <b-form-select size="sm" v-model="argomento" :options="argomentoOptions"></b-form-select>                
            </div>
            <div class="titleSectionCalcolatore">
                <div class="titleCalcolatore">Hashtags</div>
                <vue-tags-input
                v-model="tag"
                :tags="tags"
                @tags-changed="newTags => tags = newTags"
            />              
            </div>
            <div class="btnModDiv">
                <b-button @click="onSubmitModifica(id)" class="button btnModifica">MODIFICA</b-button>      
            </div>
                  
            </b-form>
        </b-modal>

        </div>
</template>

<script>
import { mapGetters,mapActions, mapMutations } from 'vuex';
import VueTagsInput from '@johmun/vue-tags-input';
export default {
    components: {
        VueTagsInput,
    },
    data(){return{
        tagFilter:'',
        tagsFilter:[],
        tag:'',
        tags: [],
        id:'',
        titolo: '',
        urlVideo: '',
        data:'',
        argomento: 'Mutui',
        argomentoOptions:['Mutui','Prestiti','Cessione Del Quinto','Assicurazioni','Spot','Formazione Societ Hunica'],
        filterArgomentoOptions:['Tutti','Mutui','Prestiti','Cessione Del Quinto','Assicurazioni','Spot','Formazione Societ Hunica'],
        filterArgomentoSelected: 'Tutti',
    }},
    computed: mapGetters(['formazioni','formazioniRetrived','filtredTags','filtredFormazioni']),
    watch:{
        'tagFilter':'setupTagsFun',
        'tagsFilter':'filtring',
        'filterArgomentoSelected':'filtring',
    },
    methods:{
        ...mapMutations(["setTags","setupTags","filtringFormazioni"]),
        ...mapActions(["getAllFormazioni","deleteFormazione","updateFormazione"]),
        cancella(id){
            console.log('Deleting: '+id);
            this.deleteFormazione(id).then(()=>{
                this.$parent.makeToast('Formazione Cancellata',500,'info',true);
            }).catch(()=>{
                this.$parent.makeToast('Errore',500,'danger',true);
            });
        },
        hideModal(id){
            this.$bvModal.hide(id);
        },
        onSubmitModifica(id){
            var formazione = {
                _id: id,
                titolo: this.titolo,
                urlVideo: this.urlVideo,
                argomento: this.argomento,
                data: this.data,
                hashtag: [],
            }
            this.tags.forEach(tag => {
                formazione.hashtag.push(tag.text);
            });
            this.updateFormazione(formazione).then(()=>{
                this.$parent.makeToast('Formazione Modificata',500,'success',true);
                this.$bvModal.hide('modifica');
            }).catch(()=>{
                this.$parent.makeToast('Errore',500,'danger',true);
            });
        },
        opendModal(id){
            const index = this.formazioni.findIndex(element => element._id == id);
            if(index != -1){
                this.id = id;
                this.titolo = this.formazioni[index].titolo;
                this.urlVideo = this.formazioni[index].urlVideo;
                this.argomento = this.formazioni[index].argomento;
                this.data = this.formazioni[index].data;
                this.tags = [];
                this.formazioni[index].hashtag.forEach(tag=>{
                    this.tags.push({text:tag});
                })
                this.$bvModal.show('modifica');
            }else{
                this.$parent.makeToast('Errore',500,'danger',true);
            }
            
        },
        reformatDate(date){
            var data = new Date(date);
            return data.getDate()+'/'+(data.getMonth()+1)+'/'+data.getFullYear();
        },
        setupTagsFun(){
            this.setupTags(this.tagFilter);
        },
        filtring(){
            var filters = {
                tags:this.tagsFilter,
                argomento:this.filterArgomentoSelected
            }
            this.filtringFormazioni(filters);
        }
    },
    mounted(){
        if(!this.formazioniRetrived){
            this.getAllFormazioni().then(()=>{
                this.setTags();
            })
        }else{this.setTags();}
    }
}
</script>

<style scoped>
    #adminFormazione{
        margin-top: calc(var(--NavBarHeight) + 50px);   
        margin-bottom: 40px;
    }
    .container{
        width: 100%;
        text-align: left;
        padding: 20px 20px 60px 20px;
        max-width: 1000px;
    }
    .element{
        border-radius: 10px;
        box-shadow: 0 2px 2px 0px rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
        padding-top: 10px;
    }
    .title{
        padding: 5px 10px 5px 10px;
        margin-left: 10px;
        margin-right: 10px;
        margin-bottom: 5px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.14);
        color: rgba(0, 0, 0, 0.705);
    }
    .descrizioneTesto{
        font-size: 10pt;
        line-height: 14px;
        margin-bottom: 0px;
    }
    .blocco{
        margin-top: 20px;
        margin-bottom: 20px;
        position: relative;
    }
    .description{
        padding: 5px 10px 5px 10px;
        margin-left: 10px;
        margin-right: 10px;
        border-top: 1px solid rgba(0, 0, 0, 0.14);
    }
    .argomento{
        font-size: 7pt;
        text-transform: uppercase;
        font-weight: 800;
        color: white;
        border: 1px solid var(--complementaryC);
        background-color: var(--complementaryC);
        padding: 2px 4px 2px 4px;
        border-radius: 5px;
        width: max-content;
        margin-left: 10px;
    }
    .data{
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        font-size: 8pt;
        text-transform: uppercase;
        font-weight: 500;
        color: white;
        border: 1px solid var(--complementaryC);
        background-color: var(--complementaryC);
        padding: 1px 3px 1px 3px;
        border-radius: 5px;
        width: max-content;
        margin-left: 10px;
    }
    .hashtag{
        float: left;
        margin: 5px 2px 0px 2px;
        border: 1px solid var(--complementaryC);
        background-color: var(--complementaryC);
        padding: 1px 3px 1px 3px;
        font-size: 9pt;
        color: white;
        border-radius: 5px;
    }
    .actions{
        margin-top: 10px;
        margin-left: 20px;
        margin-right: 20px;
        padding-bottom: 10px;
    }
    .button{
        width: 100%;
        text-align: center;
        font-weight: 500;
        border: 1px solid black;
        border-radius: 5px;
        cursor: pointer;
    }
    .btnModifica{
        border-color: rgb(57, 87, 255);
        background-color: white;
        color: rgb(57, 87, 255);
    }
    .btnModifica:hover{
        background-color: rgb(57, 87, 255);
        color: white;
    }

    .btnCancella{
        border-color: rgb(255, 55, 55);
        color: rgb(255, 55, 55);
    }
    .btnCancella:hover{
        background-color: rgb(255, 55, 55);
        color:white;
    }
    .vue-tags-input{
        max-width: 100%;
    }
    .filters{
        box-shadow: 0 2px 2px 0px rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);;
        width: 100%;
        background-color: white;
        text-align: center;
        padding: 0px 20px 10px 20px;
        max-width: 1000px;
        border: 2px solid var(--complementaryC);
    }
    .btnModDiv{
        margin: 20px auto 0px auto;
        padding: 0px 20px 0px 20px;
    }
    .titleSectionCalcolatore{
        text-align: left;
        margin-top: 10px;
        margin-bottom: 10px;
        display: block;
    }
    .titleCalcolatore{
        font-weight: bold;
        font-style: normal;
    }
    .asterisco{
        color: var(--complementaryC);
    }
    .subTitleCalcolatore{
        font-weight: 400;
        font-size: 0.8em;
        color: gray;
        margin-top: -4px;
        font-style: italic;
    }


</style>