                </div>
                <div class="titleSectionCalcolatore">
                    <div class="titleCalcolatore">Data di Nascita</div>
                    <b-form-datepicker id="example-datepicker" size="sm" v-model="form.dataDiNascita" class="mb-2"></b-form-datepicker>
                </div>
                <b-row>
                    <b-col sm="3">
                        <div class="titleSectionCalcolatore">
                            <div class="titleCalcolatore">% Mutui</div>
                            <b-form-input id="input-2 " size="sm" v-model="form.percMutui" placeholder="(ex. 60.00)"></b-form-input>
