                    </b-col>
                    <b-col sm="3">
                        <div class="titleSectionCalcolatore">
                            <div class="titleCalcolatore">% CDQ</div>
                            <b-form-input id="input-2 " size="sm" v-model="form.percCDQ" placeholder="(ex. 60.00)"></b-form-input>
