</template>

<script>
import { mapGetters,mapActions } from 'vuex';
export default {
    data(){
        return{
            loading: false,
            ruoloFields:['Dipendente','Agente'],
            figuraFields:["Head Executive","Top Executive","Executive","Team Leader","Personal Broker","Dipendente"],
            sessoFields:['M','F'],
            form:{
                nome:'',
                cognome:'',
                email:'',
                ruolo:'',
                figura:'',
                sesso:'',
                indirizzo:'',
                civico:'',
                comune:'',
                provincia:'',
                cap:'',
                regione:'',
                cellulare: '',
                codiceFiscale:'',
                dataDiNascita:'',
                percMutui:'',
                percCDQ:'',
                percMediazioni:'',
                perProvPrestiti:'',
                oam:'',
                topExecutive:'',
                executive:'',
                teamLeader:'',
            }
        }
    },
    computed: mapGetters(['agentiExecutive','agentiTeamLeader','agentiTopExecutive','retrived']),
    methods:{
        ...mapActions(["fetchAgenti","createAgente"]),
        onSubmit(evt){
            evt.preventDefault();
            console.log(this.form);
            this.createAgente(this.form).then(() =>{
                this.$parent.makeToast('Utente Creato',500,'success',true);
            });
        },
        onReset(evt){
            evt.preventDefault();
        },
        filterFigura(figura,element){
            return element.figura == figura;
        },
    },mounted(){
        if(!this.retrived){
            this.fetchAgenti()
        }
    }
}
</script>

