        margin-top: 10px;
        width: 100%;
        max-width: 200px;
        background-color: white;
        border: 1px solid green;
        color: green;
        font-weight: 700;
    }
    .btnSubmit:hover{
        background-color: green;
        color: white;
    }
    .subhr{
        width: 100%;
        text-align: center;
