import axios from 'axios';
import { Server } from '../../utils/variables';

const state = {
    agenzie:[],
    retrived: false,

}

const getters = {
    agenzie: state => state.agenzie,
    agenzieRetrived: state => state.retrived,
}

const actions = {
    //async actions
    async getAllAgenzie({ commit }){
        var response = await axios.get(Server+'/agenzie/getAll');
        commit('setAllAgenzie', response.data.agenzie);
    }

}

const mutations = {
    //sync actions
    setAllAgenzie(state,agenzie){
        state.agenzie = agenzie;
        console.log('Agenzie Retrived');
        state.retrived = true;
    }
}

export default{
    state,
    getters,
    actions,
    mutations
}