import axios from 'axios';
import { Server } from '../../utils/variables';
import cookie from 'vue-cookies'

const state = {
    news:[],
    newsSingola:{},
    retrived:false,
}

const getters = {
    news: state => state.news,
    limitedNews: state => state.news.slice(0,4),
    retrivedNews: state => state.retrived,
    newsSingola: state => state.newsSingola,
}

const actions = {
    //async actions
    async getAllNews({ commit }){
        const response = await axios.get(Server+'/news/getAllNews');
        commit('setAllNews',response.data);
    },
    async deleteNews({ commit },id){
        const config = { headers: { Authorization: `Bearer ${cookie.get('adminToken')}` }};
        await axios.post(Server+'/news/deleteNews',{
            id: id,
        },config);
        commit('cancelNews',id);
    },
    async createNews({commit},news){
        const config = { headers: { Authorization: `Bearer ${cookie.get('adminToken')}` }};
        var response = await axios.post(Server+'/news/addNews',{
            title: news.title,
            data: news.data,
            urlImg: news.urlImg,
            html: news.html
        },config)
        commit('addNews',response.data);
    },
    async editNews({commit},update){
        const config = { headers: { Authorization: `Bearer ${cookie.get('adminToken')}` }};
        var response = await axios.post(Server+'/news/editNews',{
            id: update.id,
            title: update.title,
            data: update.data,
            urlImg: update.urlImg,
            html: update.html
        },config);
        commit('modificaNews',response.data.result);
    }

}

const mutations = {
    //sync actions
    setAllNews(state,news){
        state.news = news;
        state.retrived = true;
        console.log('News Retrived');        
    },
    cancelNews(state,id){
        const index = state.news.findIndex(news => news._id == id);
        if(index != -1){
            state.news.splice(index, 1);
        }
    },
    setNewsSingola(state,id){
        state.newsSingola = state.news.find(element => element._id == id);
    },
    addNews(state,news){
        news["isNew"] = true;
        state.news.unshift(news);
    },
    modificaNews(state,news){
        const index = state.news.findIndex(element => element._id == news._id);
        if(index != -1){
            state.news[index] = news;
        }
    }
}

export default{
    state,
    getters,
    actions,
    mutations
}