<template>
    <div id="adminAgenti">
        <p class="subtitle fancy"><span>LISTA <b>AGENTI</b></span></p>
        <div class="container">
                <template v-if="!retrived">
                    <b-row>
                        <b-col sm="6" class="listColumn" v-for="index in 50" :key="index"><b-skeleton animation="wave" class="skeleton" width="100%"></b-skeleton></b-col>

                    </b-row>
                </template>


            <div class="filters">
                <b-row class="my-1">
                    <b-col sm="6">
                        <label for="input-small" class="overInput">Name o Cognome:</label>
                        <b-form-input id="input-small" size="sm" placeholder="Inserisci Nome o Cognome" v-model="nameFilterInput" @change="Filtring"></b-form-input>
                    </b-col>
                    <b-col sm="6">
                        <label for="input-small" class="overInput">Figura:</label>
                        <b-form-select size="sm" v-model="figuraSelected" :options="figuraOptions" @change="Filtring"></b-form-select>                
                    </b-col>
                </b-row>
            </div>

            <transition name="fade">
                <b-list-group class="list" v-if="retrived">
                    <b-row>
                        <b-col md="6" class="listColumn" :key="agente._id" v-for="agente in filtredAgenti">
                            <b-list-group-item button  v-b-modal="agente._id.toString()" class="item">
                                <img src="../../assets/imgs/empty-profile.png" alt="" class="imgProfile">
                                <span class="spanNome">{{agente.nome}}</span> <span class="spanCognome">{{agente.cognome}}</span>  <span class="spanFigura">{{agente.figura}}</span>
                                <DisplayAgente :agente=agente :index=agente._id></DisplayAgente>
                            </b-list-group-item>
                        </b-col>
                    </b-row>
                </b-list-group>
            </transition>
        </div>


    </div>
</template>

<script>
import { mapGetters, mapMutations,mapActions } from 'vuex';
import DisplayAgente from "../../components/admin/adminAgenti/displayAgente";
export default {
    name: 'Agenti',
    components:{
        DisplayAgente,
    },
    data(){return{
        loading: false,
        figuraOptions: ["Tutti","Head Executive","Top Executive","Executive","Team Leader","Personal Broker"],
        figuraSelected: "Tutti",
        nameFilterInput: '',
    }},
    mounted(){
        console.log(this.retrived);
        if(!this.retrived){
            this.fetchAgenti();
        }
    },
    methods:{
        ...mapMutations(['setAgentiFiltrati']),
        ...mapActions(["fetchAgenti"]),
        Filtring(){
            window.scrollTo({ top: 0, behavior: 'smooth' });
            this.setAgentiFiltrati({name:this.nameFilterInput,figura:this.figuraSelected});
        },
    },
    computed: mapGetters(['allAgenti','filtredAgenti','retrived']), //quando modificati li prendo
}
</script>

<style scoped>
    #adminAgenti{
        margin-top: calc(var(--NavBarHeight) + 50px);
        min-height: 80vh;
    }
    .filters{
        box-shadow: 0 2px 2px 0px rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);;
        width: 100%;
        background-color: white;
        text-align: center;
        padding: 0px 20px 10px 20px;
        max-width: 1000px;
        position: fixed;
        left: 50%;
        bottom: 0px;
        transform: translateX(-50%);
        z-index: 3;
        border: 2px solid var(--complementaryC);
        border-bottom: 0px;
    }
    .list{
        margin-top: 10px;
    }

    .listColumn{
        padding-left: 5px;
        padding-right: 5px;
        margin-bottom: 5px;
    }
        .overInput{
        margin-bottom: 0px;
        margin-top: 10px;
    }

    .item{
        border-radius: 10px;
        z-index: 2;
    }
    .skeleton{
        height: 66px;
        background-color: white;
        border: 1px solid rgba(0,0,0,.125);
        border-radius: 10px;
        cursor: pointer;
        z-index: 1;
    }
    .b-skeleton-animate-wave::after{
        background: linear-gradient(90deg, transparent, rgba(255, 229, 156, 0.4), transparent);
    }
    

    .imgProfile{
        max-height: 40px;
        max-width: 40px;
        border: 2px solid rgba(0,0,0,.125);
        margin-right: 10px;
    }
    .spanNome, .spanCognome{
        text-transform: uppercase;
        font-weight: 500;
    }
    .spanFigura{
        text-transform: uppercase;
        font-size: 8pt;
    }
    .spanFigura::before{
        content: ' - ';
    }
    button:focus{
        outline: none!important;
    }
</style>