<template>
    <div id="news">
        <div class="container">
            <img :src=news.urlImg alt="" class="imgNews">
            <div class="testo">
                <div class="titolo">{{news.title}}</div>
                <div class="data">{{formatDate(news.data)}}</div>
                <div class="html" v-html="news.html"></div>
            </div>
        </div>
    </div>

</template>

<script>
import { mapGetters,mapActions, mapMutations } from 'vuex';
export default {
    data(){
        return{
            title: '',
            urlImg:'',
            data: '',
            testo:'',
        }
    },
    computed: mapGetters({news:'newsSingola',retrivedNews:'retrivedNews'}),
    mounted(){
        if(!this.retrivedNews){
            this.getAllNews().then(()=>{
                this.setNewsSingola(this.$route.params.id);
            });
        }else{
            this.setNewsSingola(this.$route.params.id);
        }
    },methods:{
        ...mapActions(["getAllNews"]),
        ...mapMutations(["setNewsSingola"]),
        formatDate(date){
            var data = new Date(date);
            var giorno = data.getDate();
            if(giorno<10){giorno = '0'+giorno}
            var mese = data.getMonth();
            if(mese<10){mese = '0'+mese}
            var anno = data.getFullYear();
            return giorno+'/'+mese+'/'+anno;
        },
    }
}
</script>

<style scoped>
    #news{
        margin-top: calc(var(--NavBarHeight) + 10px);
        margin-bottom: 40px;
    }
    .imgNews{
        max-width: 100%;
        box-shadow: 0 2px 2px 0px rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    }
    .titolo{
        font-size: 25pt;
        color: var(--complementaryC);
        font-weight: 700;
    }
    .data{
        margin-top: -12px;
        font-weight: 800;
        margin-bottom: 10px;
    }
    .testo{
        padding-left: 20px;
        padding-right: 20px;
    }

    /* CSS TESTO */
</style>