<template>
    <div id="newsList">
        <p class="subtitle fancy"><span><b>NEWS</b> LIST</span></p>
        <div class="container">

            <b-row v-if="loading">
                <b-col md="4" v-for="index in 9" :key="index">
                    <div class="card"><b-skeleton width="100%" class="skeleton"></b-skeleton></div>
                </b-col>
            </b-row>

            <transition name="fade">
            <b-row v-if="!loading">
                <b-col v-for="notizia in news" :key="notizia._id" md="4">
                    <div class="card">
                        <img :src=notizia.urlImg alt="" class="img">
                        <div class="text">
                            <p class="date">{{reformatDate(notizia.data)}}</p>
                            <p class="title">{{notizia.title}}</p> 
                        </div>
                        <b-row class="btnRow">
                            <b-col class="btnCol"><div class="button btnVisualliza" @click="navigateTo('/news/'+notizia._id)">Visualliza</div></b-col>
                            <b-col class="btnCol"><div class="button btnModifica" @click="modifica(notizia._id)">Modifica</div></b-col>
                            <b-col class="btnCol"><div class="button btnCancella" v-b-modal="notizia._id">Cancella</div></b-col>
                            <b-modal title="SEI SICURO?" :id="notizia._id" hide-footer>
                                <b-row>
                                    <b-col><div class="button btnCancella" @click="cancella(notizia._id)">SI</div></b-col>
                                    <b-col><div class="button btnVisualliza" @click="rispostaNo(notizia._id)">NO</div></b-col>
                                </b-row>
                            </b-modal>
                        </b-row>
                    </div>
                </b-col>
            </b-row>
            </transition>

        </div>
    </div>
</template>

<script>
import { mapGetters,mapActions } from 'vuex';
export default {
    data(){
        return{
            loading: false,
        }
    },mounted(){
        if(!this.retrivedNews){
            this.getAllNews();
        }
    },
    computed: mapGetters(['news','retrivedNews']),
    methods:{
        ...mapActions(["getAllNews","deleteNews"]),
        reformatDate(date){
            var data = new Date(date);
            return data.getDate()+'/'+(data.getMonth()+1)+'/'+data.getFullYear();
        },
        cancella(id){
            try{
                this.deleteNews(id).then(()=>{
                    this.$parent.makeToast('News Cancellata',500,'info',true);
                })
            }catch(e){
                console.log(e);
            }
        },
        rispostaNo(id){
            this.$bvModal.hide(id);
        },
        filterList(id,element){
            return element._id != id;
        },
        modifica(id){
            window.location.href = "/admin/editNews/"+id;
        },
        navigateTo(path){
            this.$router.push(path).catch(() =>{});
        }
    }
}
</script>


<style scoped>
    #newsList{
        margin-top: calc(var(--NavBarHeight) + 50px);   
        margin-bottom: 40px;
    }
    .img{
        width:100%;
    }
    .card{
        margin-top: 10px;
        padding-left: 0px;
        padding-right: 0px;
    }
    .text{
        padding-top: 5px;
        padding-left: 10px;
        padding-right: 10px;
    }
    .date{
        margin-bottom: 0px;
        font-size:9pt;
        font-weight: 800;
        background-color: var(--complementaryC);
        color: white;
        border-radius: 5px;
        width: max-content;
        padding: 0px 5px 0px 5px;
    }
    .title{
        margin-bottom: 0px;
        margin-top: 0px;
        font-weight: 700;
        overflow: hidden;
        height: 50px;
    }
    .btnRow{
        padding-left: 15px;
        padding-right: 15px;
        margin-bottom: 10px;
    }
    .btnCol{
        margin-top: 10px;
        padding-left: 5px;
        padding-right: 5px;
    }
    .button{
        width: 100%;
        text-align: center;
        font-weight: 500;
        border: 1px solid black;
        border-radius: 5px;
        cursor: pointer;
    }
    .btnVisualliza{
        border-color: rgb(57, 87, 255);
        color: rgb(57, 87, 255);
    }
    .btnVisualliza:hover{
        background-color: rgb(57, 87, 255);
        color: white;
    }

    .btnModifica{
        border-color: rgb(216, 97, 28);
        color: rgb(216, 97, 28);
    }
    .btnModifica:hover{
        background-color: rgb(216, 97, 28);
        color: white;
    }
    .btnCancella{
        border-color: rgb(255, 55, 55);
        color: rgb(255, 55, 55);
    }
    .btnCancella:hover{
        background-color: rgb(255, 55, 55);
        color:white;
    }
    .skeleton{
        width: 100%;
        height: 281px;
        margin-bottom: 0px;
        
    }
</style>