<template>
    <div id="displayAgente">
        <b-modal :id=agente._id :title="capitalize(agente.nome) + ' ' + capitalize(agente.cognome)" hide-footer @hidden="onHidden">
            
            <div class="preIMG">
                <img :src="photo" alt="" class="imgProfile">
            </div>
            <div class="stats">
                <p class="info"><b>Email:</b> {{agente.email}}</p>
                <p class="info"><b>Stato Account:</b> {{agente.status=="pending"?'Da Confermare':'Confermato' }}</p>
                <p class="info"><b>Ruolo:</b> {{capitalize(agente.ruolo)}}</p>
                <p class="info"><b>Figura:</b> {{capitalize(agente.figura)}}</p>
                <p class="info"><b>Data Inizio:</b> {{new Date(agente.dataDiNascita).getDate()}}/{{new Date(agente.dataDiNascita).getMonth()+1}}/{{new Date(agente.dataDiNascita).getFullYear()}}</p>
                <p class="info"><b>Indirizzo:</b> {{capitalize(agente.indirizzo)}} - {{agente.civico}}</p>
                <p class="info"><b>Comune(Provincia):</b> {{capitalize(agente.comune)}} ({{agente.provincia.toUpperCase()}})</p>
                <p class="info"><b>CAP/Regione:</b> {{agente.cap}} - {{capitalize(agente.regione)}}</p>
                <p class="info"><b>Codice Fiscale:</b> {{agente.codiceFiscale}}</p>
                <p class="info"><b>Cellulare:</b> {{agente.cellulare}}</p>
                <p v-if="agente.topExecutive" class="info"><b>Top Executive: </b>{{capitalize(agente.topExecutive.nome)}} {{capitalize(agente.topExecutive.cognome)}}</p>
                <p v-if="agente.executive" class="info"><b>Executive: </b>{{capitalize(agente.executive.nome)}} {{capitalize(agente.executive.cognome)}}</p>
                <p v-if="agente.teamLeader" class="info"><b>Team Leader: </b>{{capitalize(agente.teamLeader.nome)}} {{capitalize(agente.teamLeader.cognome)}}</p>
                <p v-if="agente.votazioni[0]" class="info"><b>Votato Questo Mese:</b> {{agente.votazioni[0].voto_effettuato?'Si':'No'}}</p>

                <a href=""><div class="btnProfile publicProfileBtn">PUBLIC PROFILE</div></a>
                <a @click="modifica"><div class="btnProfile editBtn">MODIFICA</div></a>
            </div>       
        </b-modal>
    </div>
</template>

<script>
export default {
    data(){
        return{
            photo: '',
        }
    },
    props: ['agente','index'],
    mounted(){
        if(this.agente.urlPhoto){this.photo = this.agente.urlPhoto}else{
            this.photo = require('../../../assets/imgs/empty-profile.png');
        }

    },methods:{
        capitalize(s){
            if (typeof s !== 'string') return ''
            return s.charAt(0).toUpperCase() + s.slice(1)
        },
        onHidden() {
            this.editMode = false;
        },
        modifica(){
            this.$router.push('editAgente/'+this.agente._id);
        }
    }
}
</script>
<style scoped>
    .info{
        margin-top: 5px;
        margin-bottom: 5px;
    }

    .preIMG{
        text-align: center;
        width: 100%;
        margin-bottom: 10px;
    }
    .imgProfile{
        max-width: 50%;
    }

    .btnProfile{
        border: 1px solid;
        width: max-content;
        padding: 5px 10px 5px 10px;
        margin-bottom: 5px; 
        margin-right: 5px;
        float: left;
        font-weight: 600;
    }
    a:hover{
        text-decoration: none;
    }

    .publicProfileBtn{
        border-color: var(--complementaryC);
        color: var(--complementaryC);
    }
    .publicProfileBtn:hover{
        background-color: var(--complementaryC);
        color: white;
    }
    .editBtn{
        border-color: rgb(68, 68, 255);
        color: rgb(68, 68, 255);
        cursor: pointer;
    }
    .editBtn:hover{
        background-color: rgb(68, 68, 255);
        color: white;
    }

    .inpcol{
        padding-left: 0px;
        padding-right: 0px;
    }
    .inprow{
        padding-left: 15px;
        padding-right: 15px;
    }
</style>