import axios from 'axios';
import { Server } from '../../utils/variables';
import cookie from 'vue-cookies'

const state = {
    formazioni:[],
    filtredFormazioni:[],
    retrived: false,
    tags:[],
    filtredTags:[],
}

const getters = {
    formazioni: state => state.formazioni,
    formazioniRetrived: state => state.retrived,
    getTags: state => state.tags,
    filtredTags: state => state.filtredTags,
    filtredFormazioni: state => state.filtredFormazioni,
}

const actions = {
    //async actions
    async getAllFormazioni({ commit }){
        const config = {headers: { Authorization: `Bearer ${cookie.get('adminToken')}` }};
        var response = await axios.get(Server+'/formazione/getAll',config);
        commit('setAllFormazioni', response.data.formazioni);
    },
    async createFormazione({ commit },formazione){
        const config = {headers: { Authorization: `Bearer ${cookie.get('adminToken')}` }};
        var response = await axios.post(Server+'/formazione/add',{
            formazione: formazione,
        },config);
        commit('addFormazione', response.data.formazione);
    },
    async deleteFormazione({ commit },id){
        const config = {headers: { Authorization: `Bearer ${cookie.get('adminToken')}` }};
        var response = await axios.post(Server+'/formazione/delete',{
            id: id
        },config);
        if(response.data.formazione.deletedCount != 0){
            commit('removeFormazione',response.data); 
        }else{
            throw 'Error';
        }
        
    },
    async updateFormazione({commit},formazione){
        const config = {headers: { Authorization: `Bearer ${cookie.get('adminToken')}` }};
        var response = await axios.post(Server+'/formazione/update',{
            formazione: formazione
        },config);
        commit('editFormazione',response.data.formazioneUpdated);
    }

}

const mutations = {
    //sync actions
    setAllFormazioni(state,formazioni){
        state.formazioni = formazioni;
        state.filtredFormazioni = formazioni;
        console.log('Formazioni Retrived');
        state.retrived = true;
    },
    addFormazione(state,formazione){
        state.formazioni.unshift(formazione);
        console.log(formazione);
    },
    removeFormazione(state,formazione){
        const index = state.formazioni.findIndex(element => element._id == formazione.id);
        if(index != -1){
            state.formazioni.splice(index, 1);
        }
    },
    editFormazione(state,formazione){
        const index = state.formazioni.findIndex(element => element._id == formazione._id);
        if(index != -1){
            state.formazioni[index] = formazione;
        }
    },
    setTags(state){
        var tags = [];
        state.formazioni.forEach(formazione => {
            formazione.hashtag.forEach(tag=>{
                if(!tags.includes(tag)){
                    tags.push({text:tag});
                } 
            })  
        });
        state.tags = tags;
    },
    setupTags(state,word){
        var rTags=[];
        state.tags.forEach(tag=>{
            if(tag.text.includes(word)){
                rTags.push(tag);
            }
        })
        state.filtredTags = rTags;
    },
    filtringFormazioni(state,filters){
        console.log('Filtring: ');
        console.log(filters.tags);
        console.log(filters.argomento);
        var filtred = state.formazioni;
        if(filters.argomento!='Tutti'){
            filtred = filtred.filter(element => element.argomento == filters.argomento);
        }
        filters.tags.forEach(tag => {
            filtred = filtred.filter(element => element.hashtag.includes(tag.text))
        })
        state.filtredFormazioni = filtred;
        console.log(filtred);
    }
}

export default{
    state,
    getters,
    actions,
    mutations
}