<template>
    <div id="editNews">
        <p class="subtitle fancy"><span>MODIFICA <b>NEWS</b></span></p>
        <div class="container" v-if="!anteprimaMod">
            <h4><i class="material-icons-outlined infoIcon">contact_page</i> <span class="contTop">CREA NEWS</span></h4>
            <b-form @submit="onSubmit" @reset="onReset">

                <div class="titleSectionCalcolatore">
                    <div class="titleCalcolatore">Titolo<span class="asterisco">*</span></div>
                </div>
                <b-form-input id="input-2" v-model="news.title" required placeholder="Inserisci il titolo"></b-form-input>

                <div class="titleSectionCalcolatore">
                    <div class="titleCalcolatore">Data<span class="asterisco">*</span></div>
                </div>
                <b-form-datepicker id="example-datepicker" v-model="news.data" class="mb-2" required></b-form-datepicker>

                <div class="titleSectionCalcolatore">
                    <div class="titleCalcolatore">Url Immagine<span class="asterisco">*</span></div>
                </div>
                <b-form-input id="input-2" v-model="news.urlImg" required placeholder="Inserisci link dell'immagine"></b-form-input>

                <div class="titleSectionCalcolatore">
                    <div class="titleCalcolatore">Testo<span class="asterisco">*</span></div>
                </div>

                <ckeditor  v-model="news.html" :config="editorConfig"></ckeditor>
                <div class="button btnVisualliza" @click="anteprimaMod=true">Anteprima</div>
                <div class="submitDiv">
                    <b-button type="submit" class="btnSubmit">MODIFICA</b-button>    
                </div>
            </b-form>
        </div>

        <div class="container" v-if="anteprimaMod">
            <img :src=news.urlImg alt="" class="imgNews">
            <div class="testo">
                <div class="titolo">{{news.title}}</div>
                <div class="data">{{reformatDate(news.data)}}</div>
                <div class="html" v-html="news.html"></div>
            </div>
            <div class="button btnVisualliza indietro" @click="anteprimaMod=false">Indietro</div>
        </div>
    </div>
</template>

<script>

import { mapGetters,mapActions, mapMutations } from 'vuex';
export default {
    data(){
        return{
            editorConfig: {
                    toolbar: [ [ 'Bold','Italic','Strike','-','Link','Image','-','NumberedList','BulletedList','-','Format','-','Source', ] ]
            },
            anteprimaMod: false,
        }
    },mounted(){
        if(!this.retrivedNews){
            this.getAllNews().then(()=>{
                this.setNewsSingola(this.$route.params.id);
            });
        }else{
            this.setNewsSingola(this.$route.params.id);
        }
    },
    computed: mapGetters({news:'newsSingola',retrivedNews:'retrivedNews'}),
    methods:{
        ...mapActions(["getAllNews","editNews"]),
        ...mapMutations(["setNewsSingola"]),
        onSubmit(evt){
            evt.preventDefault();
            var update = {
                id: this.$route.params.id,
                title: this.news.title,
                data: this.news.data,
                urlImg: this.news.urlImg,
                html: this.news.html
            }
            this.editNews(update).then(()=>{
                this.$parent.makeToast('News Modificata',500,'success',true);
            });
        },
        onReset(evt){evt.preventDefault();},
        reformatDate(date){
            var data = new Date(date);
            return data.getDate()+'/'+(data.getMonth()+1)+'/'+data.getFullYear();
        },
    }
}
</script>

<style scoped>
    #editNews{
        margin-top: calc(var(--NavBarHeight) + 50px);   
        margin-bottom: 40px;
    }
    .container{
        box-shadow: 0 2px 2px 0px rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
        width: 100%;
        background-color: white;
        text-align: left;
        padding: 20px 20px 60px 20px;
    }
    h4{
        color: var(--complementaryC);
        font-weight: 300;
        font-size: 14pt;
    }
    .titleSectionCalcolatore{
        text-align: left;
        margin-top: 10px;
        margin-bottom: 10px;
        display: block;
    }
    .titleCalcolatore{
        font-weight: bold;
        font-style: normal;
    }
    .asterisco{
        color: var(--complementaryC);
    }
    .subTitleCalcolatore{
        font-weight: 400;
        font-size: 0.8em;
        color: gray;
        margin-top: -4px;
        font-style: italic;
    }
    .submitDiv{
        width: 100%;
        text-align: center;
    }
    .btnSubmit{
        margin-top: 10px;
        width: 100%;
        max-width: 200px;
        background-color: white;
        border-color: rgb(216, 97, 28);
        color: rgb(216, 97, 28);
        font-weight: 700;
    }
    .btnSubmit:hover{
        background-color: rgb(216, 97, 28);
        color: white;
    }

    .button{
        width: 100%;
        max-width: 150px;
        text-align: center;
        font-weight: 500;
        border: 1px solid black;
        border-radius: 5px;
        cursor: pointer;
        margin: 10px auto 0px auto;
    }
    .btnVisualliza{
        border-color: rgb(57, 87, 255);
        background-color: #fff;
        color: rgb(57, 87, 255);
    }
    .indietro{
        position: fixed;
        top: calc(var(--NavBarHeight) + 0px);
        height: 30px;
        line-height: 30px;
        text-transform: uppercase;
        left: 50%;
        transform: translateX(-50%);
    }
    .btnVisualliza:hover{
        background-color: rgb(57, 87, 255);
        color: white;
    }

    /* anteprima */
        .imgNews{
        max-width: 100%;
        box-shadow: 0 2px 2px 0px rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    }
    .titolo{
        font-size: 25pt;
        color: var(--complementaryC);
        font-weight: 700;
    }
    .data{
        margin-top: -12px;
        font-weight: 800;
        margin-bottom: 10px;
    }
    .testo{
        padding-left: 20px;
        padding-right: 20px;
    }
</style>