import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Mutui from '../views/Mutui.vue'
import Prestiti from '../views/Prestiti.vue'
import CessioneDelQuinto from '../views/Cessionedelquinto.vue'
import Contattaci from '../views/Contattaci.vue'
import PrivacyPolicy from '../views/PrivacyPolicy.vue'
import Trasparenza from '../views/Trasparenza.vue'
import TrasparenzaCalcolatori from '../views/TrasparenzaCalcolatori.vue'
import PageNotFound from '../views/PageNotFound.vue'

import AreaRiservata from '../views/AreaRiservata.vue'
import AreaRiservataLogin from '../views/area_riservata/Login.vue'
import AreaRiservataImpostazioni from '../views/area_riservata/Impostazioni.vue'

import Admin from '../views/Admin.vue'
import AdminHome from '../views/admin/AdminHome.vue'
import AdminLogin from '../views/admin/AdminLogin.vue'
import AdminAgenti from '../views/admin/AdminAgenti.vue'
import AdminCreaAgente from '../views/admin/AdminCreateAgente.vue'
import AdminEditAgente from '../views/admin/AdminEditAgente.vue'
import AdminListaAccessi from '../views/admin/AdminListaAccessi.vue'
import AdminCreateNews from '../views/admin/AdminCreateNews.vue'
import AdminNewsList from '../views/admin/AdminNewsList.vue'
import AdminEditNews from '../views/admin/AdminEditNews.vue'
import AdminTassi from '../views/admin/AdminTassi.vue'
import AdminFormazione from '../views/admin/AdminFormazione.vue'
import AdminCreaFormazione from '../views/admin/AdminCreaFormazione.vue'

import AreaRiservataPaginaPersonale from '../views/area_riservata/PaginaPersonale.vue'

import News from '../views/News.vue'
import AllNews from '../views/AllNews.vue'

Vue.use(VueRouter)


const routes = [
  { path: '/', name: 'Home', component: Home},
  { path: "*", component: PageNotFound},
  { path: '/Home', redirect:'/'},
  { path: '/home', redirect:'/'},
  { path: '/index', redirect:'/'},
  { path: '/index.html', redirect:'/'},
  { path: '/mutui', name: 'Mutui', component: Mutui},
  { path: '/prestiti', name: 'Prestiti', component: Prestiti},
  { path: '/cessionedelquinto', name: 'Cessione Del Quinto', component: CessioneDelQuinto},
  { path: '/contattaci', name: 'Contattaci', component: Contattaci},
  { path: '/privacy-policy', name: 'Privacy Policy', component: PrivacyPolicy},
  { path: '/trasparenza', name: 'Trasparenza', component: Trasparenza},
  { path: '/trasparenzacalcolatori', name: 'Trasparenza Calcolatori', component: TrasparenzaCalcolatori},
  { path: '/news/:id', name: 'News', component: News},
  { path: '/news', name: 'Tutte Le News', component: AllNews},
  { path: '/admin', name: 'Admin', component: Admin,
      children:[
        {path: '', name: 'Admin - Hub', component: AdminHome},
        {path: 'login', name: 'Admin - Login',component: AdminLogin},
        {path: 'agenti', name: 'Admin - Agenti',component: AdminAgenti},
        {path: 'nuovoAgente', name: 'Admin - Crea Agente',component: AdminCreaAgente},
        {path: 'editAgente/:id', name: 'Admin - Modifica Agente',component: AdminEditAgente},
        {path: 'lista-accessi', name: 'Admin - Lista Accessi',component: AdminListaAccessi},
        {path: 'nuovaNews', name: 'Admin - Crea News',component: AdminCreateNews},
        {path: 'newsList', name: 'Admin - News List', component: AdminNewsList},
        {path: 'editNews/:id', name: 'Admin - Edit News', component: AdminEditNews},
        {path: 'tassi', name: 'Admin - Tassi', component: AdminTassi},
        {path: 'videoFormazione', name: 'Admin - Formazione', component: AdminFormazione},
        {path: 'creaVideoFormazione', name: 'Admin - Crea Formazione', component: AdminCreaFormazione},

  ]},
  { path: '/area-riservata', name:'Area Riservata', component: AreaRiservata,
      children:[
        {path: 'login', name:'Area Riservata - Login', component: AreaRiservataLogin},
        {path: 'paginaPersonale', name:'Area Riservata - Pagina Personale', component: AreaRiservataPaginaPersonale},
        {path: 'impostazioni', name:'Area Riservata - Impostazioni', component: AreaRiservataImpostazioni}
      ]}
]

const router = new VueRouter({
  // scrollBehavior() {
  //   return { x: 0, y: 0 };
  // },
  scrollBehavior(to) {
    if (to.hash) {return window.scrollTo({ top: document.querySelector(to.hash).offsetTop, behavior: 'smooth' });}
    return window.scrollTo({ top: 0, behavior: 'smooth' });
  },
  mode: 'history',
  routes,
})

export default router
