<template>
    <div id="creaFormazioni">
        <p class="subtitle fancy"><span>CREA <b>FORMAZIONE</b></span></p>
        <div class="container">
            <h4><i class="material-icons-outlined infoIcon">contact_page</i> <span class="contTop">CREA FORMAZIONE</span></h4>
            <b-form @submit="onSubmit" @reset="onReset">
                <div class="titleSectionCalcolatore">
                    <div class="titleCalcolatore">Titolo<span class="asterisco">*</span></div>
                    <b-form-input id="input-2" size="sm" v-model="titolo" required placeholder="Inserisci il Titolo"></b-form-input>
                </div>
                <div class="titleSectionCalcolatore">
                    <div class="titleCalcolatore">Descrizione</div>
                    <b-form-input id="input-2" size="sm" v-model="descrizione" placeholder="Inserisci la descrizione"></b-form-input>
                </div>
                <div class="titleSectionCalcolatore">
                    <div class="titleCalcolatore">Data<span class="asterisco">*</span></div>
                    <b-form-datepicker id="example-datepicker" size="sm" v-model="data" class="mb-2" required></b-form-datepicker>
                </div>
                <div class="titleSectionCalcolatore">
                    <div class="titleCalcolatore">Indirizzo Video<span class="asterisco">*</span></div>
                    <b-form-input id="input-2" size="sm" v-model="urlVideo" required placeholder="Inserisci l'indirizzo video"></b-form-input>
                </div>
                <div class="titleSectionCalcolatore">
                    <div class="titleCalcolatore">Argomento<span class="asterisco">*</span></div>
                    <b-form-select size="sm" v-model="argomento" :options="argomentoOptions"></b-form-select>                
                </div>
                <div class="titleSectionCalcolatore">
                    <div class="titleCalcolatore">Hashtags</div>
                    <vue-tags-input v-model="tag" :tags="tags" @tags-changed="newTags => tags = newTags">
                        <div slot="autocomplete-item" slot-scope="props" class="my-item" @click="props.performAdd(props.item)">
                            {{ props.item.text }}
                        </div>
                        </vue-tags-input>         
                </div>
                <div class="submitDiv">
                    <b-button type="submit" class="btnSubmit">CREA</b-button>    
                </div>
                
            </b-form>
        </div>
    </div>
</template>

<script>
import VueTagsInput from '@johmun/vue-tags-input';
import { mapActions } from 'vuex';
export default {
    components: {
        VueTagsInput,
    },
    data(){return{
        titolo:'',
        argomentoOptions: ['Mutui','Prestiti','Cessione Del Quinto','Assicurazioni','Spot','Formazione Societ Hunica'],
        argomento:'Mutui',
        descrizione:'',
        urlVideo:'',
        data: new Date(),
        tag: '',
        tags: [],
    }},
    methods:{
        onReset(evt){
            evt.preventDefault();
            this.titolo = '';
            this.argomento = 'Mutui';
            this.urlVideo = '';
            this.descrizione = '';
            this.tag = '';
            this.tags = [];
            this.data = new Date();
        },
        onSubmit(evt){
            evt.preventDefault();
            var formazione = {
                _id: this.id,
                titolo: this.titolo,
                urlVideo: this.urlVideo,
                argomento: this.argomento,
                descrizione: this.descrizione,
                data: this.data,
                hashtag: [],
            }
            this.tags.forEach(tag => {
                formazione.hashtag.push(tag.text.toLowerCase());
            });
            this.createFormazione(formazione).then(()=>{
                this.$parent.makeToast('Formazione Creata',500,'success',true);
                this.onReset(evt);
            });
        },
        ...mapActions(["createFormazione"]),
    }
}
</script>


<style scoped>
    #creaFormazioni{
        margin-top: calc(var(--NavBarHeight) + 50px);   
        margin-bottom: 40px;
    }
    .container{
        box-shadow: 0 2px 2px 0px rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
        width: 100%;
        background-color: white;
        text-align: left;
        padding: 20px 20px 20px 20px;
        max-width: 800px;
    }
    h4{
        color: var(--complementaryC);
        font-weight: 300;
        font-size: 14pt;
    }
    .titleSectionCalcolatore{
        text-align: left;
        margin-top: 10px;
        margin-bottom: 10px;
        display: block;
    }
    .titleCalcolatore{
        font-weight: bold;
        font-style: normal;
    }
    .asterisco{
        color: var(--complementaryC);
    }
    .subTitleCalcolatore{
        font-weight: 400;
        font-size: 0.8em;
        color: gray;
        margin-top: -4px;
        font-style: italic;
    }
    .vue-tags-input{
        max-width: 100%;
    }
    .submitDiv{
        width: 100%;
        text-align: center;
    }
    .btnSubmit{
        margin-top: 10px;
        width: 100%;
        max-width: 200px;
        background-color: white;
        border: 1px solid green;
        color: green;
        font-weight: 700;
    }
    .btnSubmit:hover{
        background-color: green;
        color: white;
    }
</style>