import axios from 'axios';
import { Server } from '../../utils/variables';
// import cookie from 'vue-cookies'

const state = {
    TAN_FISSO:'',
    TAN_VARIABILE:'',
    TAEG_FISSO:'',
    TAEG_VARIABILE:'',
    TASSO_MEDIO:'',
    tassiRetrived: false
}

const getters = {
    getTassi: (state) =>{
        return{
            TAN_FISSO: state.TAN_FISSO,
            TAN_VARIABILE: state.TAN_VARIABILE,
            TAEG_FISSO: state.TAEG_FISSO,
            TAEG_VARIABILE: state.TAEG_VARIABILE,
            TASSO_MEDIO: state.TASSO_MEDIO,
        }
    },
    tassiRetrived: state => state.tassiRetrived, 
}

const actions = {
    //async actions
    async fetchTassi({commit}){
        var response = await axios.get(Server+'/variabili/getVariabili');
        commit('setTassi',response.data.variables);
    }

}

const mutations = {
    //sync actions
    setTassi(state,variables){
        state.TAN_FISSO = variables.TAN_FISSO;
        state.TAN_VARIABILE = variables.TAN_VARIABILE;
        state.TAEG_FISSO = variables.TAEG_FISSO;
        state.TAEG_VARIABILE = variables.TAEG_VARIABILE;
        state.TASSO_MEDIO = variables.TASSO_MEDIO;
        state.tassiRetrived = true;
        console.log('Retrived Variables');
    }
}

export default{
    state,
    getters,
    actions,
    mutations
}