<template>
    <div id="admin">
        <AdminSidebar v-if="logged"></AdminSidebar>
        <router-view/>
    </div>

</template>

<script>
import AdminSidebar from '../components/admin/adminSidebar'
import {mapMutations} from 'vuex'
export default {
    data(){
        return{
            logged: false,
        }
    },
    components: {
        AdminSidebar,
    },
    beforeRouteUpdate(to, from, next) {
        if(to.path=='/admin/agenti' || to.path=='/admin/agenti2'){
            this.resetListAgenti();
        }
        next();
    },
    mounted(){
        //redirect: tutto se non  loggato e non  sulla pagina login sulla pagina login
        if(this.$cookies.get('adminToken')==null && this.$route.name != 'Admin - Login'){
            window.location.href = "/admin/login";
        }
        if(this.$cookies.get('adminToken')!=null){  
            this.logged = true;
        }
    },methods:{
        makeToast(title,delay,variant,append) {
            this.toastCount++;
            this.$bvToast.toast(' ', {
                title: title,
                variant: variant,
                autoHideDelay: delay,
                appendToast: append,
                solid: true
            })
        },
        ...mapMutations(['resetListAgenti']),
    }
}
</script>

